.slider {
	position: relative;
	text-align: center;
	
	.slider__inner {
		width: 100vw;
		height: 75vh;
	}
	
	.slick-list,
	.slick-track,
	.slick-slide,
	.slick-slide > div {
		height: 100%;
	}
	
	&--full-height {
		.slider__inner {
			width: 100vw;
			height: 100vh;
		}
	}
	
	&__image {
		position: absolute;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		
		picture {
			position: absolute;
			z-index: 1;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
		
		img {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
	
	&__video {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		
		iframe,
		video {
			position: absolute;
		}
	}
	
	&__caption {
		position: absolute;
		top: 70%;
		left: 40px;
		right: 40px;
		color: #fff;
	}
	
	
	
	&__scroll {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 20px;
		z-index: 1000;
		
		@media screen and (max-width: $offcanvas-breakpoint) {
			bottom: 60px;
		}
		
		button {
			position: relative;
			display: inline-block;
			width: 20px;
			height: 20px;
			text-indent: -123456px;
			transition: all 0.3s ease-in-out;
			cursor: pointer;
			
			background: url('img/arrow-thin-down-white.svg') no-repeat;
			background-size: 100% 100%;
			
			&:hover,
			&:active {
				transform: translateY(5px);
			}
		}
	}
}
