@mixin slider-caption {
	font-family: $primary-font;
	font-size: 46px;
	line-height: 50px;
}


@mixin h1 {
	font-family: $primary-font;
	font-size: 40px;
	line-height: 45px;
	color: get-color('gold');
}


@mixin h2 {
	font-family: $primary-font;
	font-size: 30px;
	line-height: 40px;
	color: get-color('gold');
}


@mixin subheading {
	font-family: $secondary-font;
	font-size: 16px;
	line-height: 19px;
	color: get-color('claret');
}


@mixin price {
	font-family: $secondary-font;
	font-style: italic;
	font-size: 20px;
	line-height: 23px;
	color: get-color('claret');
}


@mixin burger-menu-sub-nav {
	font-family: $primary-font;
	font-size: 23px;
	line-height: 27px;
	color: get-color('gold');
}


@mixin body-copy {
	font-family: $secondary-font;
	font-size: 13px;
	line-height: 19px;
	color: get-color('black');
}


@mixin bullet-list {
	font-family: $secondary-font;
	font-weight: bold;
	font-size: 13px;
	line-height: 27px;
	color: get-color('black');
}


@mixin menu-list {
	font-family: $secondary-font;
	font-size: 13px;
	line-height: 27px;
	color: get-color('black');
}


@mixin terms-conditions {
	font-family: $secondary-font;
	font-size: 11px;
	line-height: 17px;
	color: get-color('black');
}


@mixin smallest {
	font-family: $secondary-font;
	font-size: 11px;
	line-height: 14px;
	text-transform: uppercase;
	color: get-color('black');
}