.header {
	position: fixed;
	z-index: 2000;
	top: 0;
	left: 0;
	width: 100%;
	background-color: $body-background;
	transition: background-color 0.3s ease-in-out;
	
	@include breakpoint(small only) {
		box-shadow: 0 0 20px rgba(#000, 0.3);
	}
	
	&__inner {
		position: relative;
		width: 100%;
	}
	
	&__open-off-canvas {
		position: absolute;
		top: 39px;
		left: 53px;
		width: 31px;
		height: 16px;
		text-indent: -123456px;
		background: url('img/burger-black.svg') no-repeat;
		background-size: 31px 16px;
		cursor: pointer;
		border: 0 !important;
		background-color: transparent !important;
		
		@media screen and (max-width: $offcanvas-breakpoint) {
			top: 24px;
			left: 20px;
		}
	}
	
	&__open-booking {
		position: absolute;
		top: 30px;
		right: 53px;
		margin: 0;
		
		@media screen and (max-width: $offcanvas-breakpoint) {
			position: fixed;
			top: auto;
			bottom: 1px;
			left: 0;
			width: 100%;
			background-color: get-color('grey');
			color: #fff;
			border: 1px solid #fff;
		}
	}
	
	&__mobile-call {
		position: absolute;
		top: 20px;
		right: 20px;
		width: 28px;
		height: 28px;
		text-indent: -12345px;
		margin: 0;
		padding: 0;
		border: 0;
		background: none transparent;
		display: none;
		
		@media screen and (max-width: $offcanvas-breakpoint) {
			display: block;
		}
		
		img,
		svg {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			
			path {
				stroke: get-color('black');
			}
		}
	}
	
	&__logo {
		width: 80%;
		max-width: 150px;
		margin: 0 auto;
		padding-top: 10px;
		
		&--transparent {
			display: none;
		}
		
		img {
			width: 100%;
		}
	}
	
	&__top-nav {
		position: absolute;
		top: 32px;

		&--left {
			left: 140px;
		}
		
		&--right {
			right: 180px;
		}
		
		@media screen and (max-width: $offcanvas-breakpoint) {
			display: none;
		}
		
		ul {
			margin: 0;
			list-style-type: none; 
		}
		
		li {
			display: inline;
			margin-left: 10px;
			
			&:first-child {
				margin-left: 0;
			} 
		}
		
		a {
			@include smallest();
		}
	}
	
	

	

	
	
	//////////////////////
	// Styles that are applied to only the transparent header
	//////////////////////
	&--transparent {
		background-color: transparent;
		background: linear-gradient(to bottom, rgba(0,0,0,0.48) 0%,rgba(0,0,0,0) 100%);
		box-shadow: none;
		padding-bottom: 40px;
		
		.header {
			&__open-off-canvas {
				background-image: url('img/burger-white.svg');
			}
			
			&__logo {
				&--transparent {
					display: block;
				}
				
				&--normal {
					display: none;
				}
			}
			
			&__top-nav {
				a {
					color: #fff;
				}
			}
			
			&__open-booking {
				background-color: transparent;
				border-color: #fff;
				color: #fff;
			}
			
			&__mobile-call svg path {
				stroke: #fff;
			}
		}
	}
}