	'black': #000,
	'white': #fff,
	'grey': #707070,
	'light-grey': #f0f0f0,
	'gold': #ad7e00,
	'claret': #810000
);

$body-background: #fff;

$primary-font: 'Times', serif;
$secondary-font: 'Helvetica Neue', sans-serif;